'use client';

import Link from 'next/link';
import { type ComponentProps, useEffect, useState } from 'react';

export type RefererLinkProps = ComponentProps<typeof Link>;

/**
 * 리퍼러 링크 atom 컴포넌트 반환 메서드
 *
 * @param {RefererLinkProps} param0: RefererLinkProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function RefererLink({ href, children, ...props }: RefererLinkProps): React.JSX.Element
{
	const [
		hrefState,
		setHrefState
	] = useState(href);

	useEffect(() =>
	{
		setHrefState(sessionStorage.getItem('referer') ?? href);
	}, [ href ]);

	return (
		<Link data-component="RefererLink" {...props} href={hrefState}>
			{children}
		</Link>
	);
}