'use client';

import { Check, Code, Copy } from 'lucide-react';
import { type DetailedHTMLProps, type HTMLAttributes, type MouseEventHandler, useState } from 'react';
import { PrismAsync } from 'react-syntax-highlighter';
import { oneDark, oneLight } from 'react-syntax-highlighter/dist/cjs/styles/prism';

import Button from '@kapoo/headless-ui-pack/components/new-york/Button';
import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import Typography from '../../atom/Typography';

export interface MarkdownCodeBlockProps extends Omit<DetailedHTMLProps<HTMLAttributes<HTMLElement>, HTMLElement>, 'ref' | 'style'>
{
	/**
	 * 테마
	 */
	readonly theme?: ThemeType;

	/**
	 * 언어명
	 */
	readonly languageName: string;
}

/**
 * 마크다운 코드블럭 organism 컴포넌트 반환 메서드
 *
 * @param {MarkdownCodeBlockProps} param0: MarkdownCodeBlockProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function MarkdownCodeblock({ theme, languageName, children, ...props }: MarkdownCodeBlockProps): React.JSX.Element
{
	const [
		isCopiedState,
		setIsCopiedState
	] = useState(false);

	// eslint-disable-next-line @typescript-eslint/no-base-to-string
	const code = String(children)
		.replace(/\n$/, '');

	const handleCopyClick: MouseEventHandler<HTMLButtonElement> = async () =>
	{
		await window.navigator.clipboard.writeText(code);

		setIsCopiedState(true);

		setTimeout(() =>
		{
			setIsCopiedState(false);
		}, 2000);
	};

	return (
		<div className={cn('mt-6 overflow-hidden rounded-lg border border-slate-200 shadow-lg dark:border-neutral-800 [&_pre]:!m-0 [&_pre]:!rounded-none')} data-component="MarkdownCodeblock">
			<div className="flex items-center justify-between border-b border-b-slate-200 bg-slate-100 px-4 py-2 dark:border-b-neutral-800 dark:bg-neutral-900">
				<div className="flex items-center gap-2">
					<Code size={14} />
					<Typography variant="small">{languageName.toUpperCase()}</Typography>
				</div>

				<div className="flex items-center gap-2">
					<div className="size-3 rounded-full bg-red-400 shadow-[0px_0px_5px] shadow-red-400" />
					<div className="size-3 rounded-full bg-orange-400 shadow-[0px_0px_5px] shadow-orange-400" />
					<div className="size-3 rounded-full bg-green-400 shadow-[0px_0px_5px] shadow-green-400" />
				</div>
			</div>

			<div className="relative">
				<div className="absolute top-2 right-2">
					<Button disabled={isCopiedState} size="icon" variant="outline" onClick={handleCopyClick}>
						{isCopiedState ? <Check className="text-green-600" /> : <Copy />}
					</Button>
				</div>

				<div className="[&_code]:!font-['Fira_Code','Pretendard']">
					<PrismAsync
						language={languageName}
						style={theme === 'light' ? oneLight : oneDark}
						showLineNumbers
						{...props}
					>
						{code.replaceAll('\t', '    ')}
					</PrismAsync>
				</div>
			</div>
		</div>
	);
}